'use strict';

window['NACSS']         = window['NACSS']         || {};
window['NACSS']['list'] = window['NACSS']['list'] || {};


((NS) => {

	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	

	/**
	 *
	 * Custom
	 *
	 * @author Takuto Yanagida
	 * @version 2022-08-04
	 *
	 */
	
	
	function apply(uls, opts = {}) {
		opts = Object.assign({
			styleList   : ':ncList',
			customType  : [],
			doClearStyle: true,
		}, opts);
	
		for (const ul of uls) {
			setStyle(ul, opts);
		}
	}
	
	function setStyle(t, opts) {
		const type = t.style.listStyleType;
		if (type !== '' && type !== 'none' && opts.customType.includes(type)) {
			if (opts.doClearStyle) {
				const re = new RegExp('list-style\\s*:\\s*' + type + '\\s*;?', 'gi');
				if (t.getAttribute('style').match(re)) {
					t.removeAttribute('style');
				} else {
					t.style.listStyleType = '';
				}
			}
			setClass(t, opts.styleList, true, type);
		}
	}
	
	NS.applyCustom = apply;

})(window['NACSS']['list']);
